export const pay = (url, orderId, channel, couponId, wapUrl, spbillCreateIp) => {
    let data = getAxios('post',
        URL + url, {
            order_id: orderId, // ID
            channel: channel, // 
            coupon_id: couponId, // ID
            type: 5, // H5 5
            wapUrl: wapUrl, //
            spbillCreateIp: spbillCreateIp, // 
            timestamp: new Date().getTime(),
        }
    );
    return data;
};
