export const signInUsers = (phone, phoneModel) => {
    let ip = localStorage.getItem('mobileId');
    let openId = localStorage.getItem('openId');
    let data = getAxios('post',
        URL + 'apiwrite/user/login', {
            phone: phone,
            jpush_id: 'H5',
            identification: 'H5',
            ip: ip,
            phoneModel: phoneModel,
            openId:openId,
            timestamp: new Date().getTime(),
        }
    );
    return data;
};

