<template>
  <div id="pay-ment" ref="payment">
    <div class="pay-ment-box" ref="payMentBox">
      <div class="content">
        <div class="coup">
          <div class="ct-info" style="margin-top: .5rem"></div>
          <div class="ct-item clearfix" @click="goCounp"><span class="coup-ic f-r"></span><span class="coup-num f-r">{{counpsTips}}</span></div>
        </div>
        <div class="payWays p-r">
          <div class="ct-info"></div>
          <div class="pay-item ct-item p-r clearfix" v-for="(item,index) in payWays" :key="item.name" @click="selectPayWays(item,index)">
            <div class="pay-ic p-a" :class="[item.class]"></div>
            <div class="pay">
              <div class="name">{{item.name}}</div>
              <div class="info">{{item.info}}</div>
            </div>
            <!---->
            <div class="choose-ic p-a" v-show="currentIndex == index"></div>
          </div>
        </div>
        <div class="comfirm t-c" @click="confirmPay"><span class="t-c">{{payAmounts}}</span></div>
      </div>
    </div>
  </div>
</template>
<script>
  import subHeader from '../reservationDt/header';
  import BScroll from 'better-scroll';
  import {Toast,Indicator} from 'mint-ui';
  import { setTimeout, setInterval, clearInterval, clearImmediate } from 'timers';
  import { pay,getMyAppointInfo,getMyCounpLists } from '../../server/getData';
  import baseURL from '../../server/baseURL';
  import { escape } from 'querystring';
  import common from '../../common/js/common.js';
  import { MessageBox } from 'mint-ui';

  export default {
    name:'payMent',
    data () {
      return {
        // headerMark:'',
        headerMark:'',
        currentIndex:0,   //
        payFees:0,  //
        // payWays:['','',''],   //
        discounts:0,   //
        myAcounts:0,   //
        counpNum:0,  //
        couponId:-1, //id
        orderId:'',  //id
        isChoosed:false, //
        onlyYuErPay: false, //
        payWays:[
          // {
          //   name:'',
          //   info:'',
          //   class:'zhifubao'
          // },
          {
            name:'',
            info:'5.0',
            class:'weixin'
          },
        ],  //
        url: "",
        type: 5,
        // zfbReturnUrl:''
        payState: false, // 
        payUrl: '', // Url
        orderState: null, //   
        timeInter: null, // ID
        time: 0,  // 
        timeflag: false, // 5
      }
    },
    components: {
      subHeader
    },
    methods: {
      //
      _initScroll(){
        this.$nextTick(()=>{
          if(!this.scroll){
            this.scroll = new BScroll(this.$refs.payMentBox,{
              probeType:3,
              scrollY:true,
              click:true
            });
          //
          }else{
            this.scroll.refresh();
          }
        });
      },
      //
      async getCounpNum(){
      // alert(2)
        let userId = localStorage.getItem('userId');
        let res = await getMyCounpLists(userId, 0);
        if(res.error_code === 2000 ){
          //TestData 
          this.counpNum = res.data.coupons.length;
        }else{
          // Toast('');
        }
      },
      //
      async confirmPay(){
        if(this.couponId === -1){
          this.couponId = null;
        }
        let timeflag = common.getStorage('H5_timeflag');
        if(timeflag){
          // 
          this.messInfo();
          return;
        }else{
            if(this.orderState === true){  // 
              this.payUrl = 'apiwrite/parking/pay'
            }else{
              // 
              this.payUrl = 'apiwrite/reserve/pay'
            }
            this.wxPay();
        }
      },

      //  
      messInfo(){
        let htmls = `
                      <div class="paymessage-info">
                        <div class="is-text"></div>
                      </div>`;
        MessageBox({
          title: '',
          message: htmls,
          showConfirmButton:true,
          confirmButtonText:'',
          showCancelButton:false,
        }).then(action=>{
          if (action == 'confirm') {
            common.removeStorage('H5_payMent_time');
            common.removeStorage('H5_timeflag');
            clearInterval(this.timeInter);
            alert(this.orderState);
            if(this.orderState){
              this.$router.push("reservationPaking")
            }else{
              localStorage.removeItem('orderId')
              this.$router.push("reservationOld")
            }
          }
        }).catch(err=>{

        })
      },
      //
      selectPayWays(item,key){
        if(this.onlyYuErPay){
          this.currentIndex = 0;
          return;
        }
        this.currentIndex = key;
        localStorage.setItem('H5_currentIndex',key);
      },

      //
      async wxPay(){
        let spbillCreateIp = localStorage.getItem("mobileId");
        let channel = 5; // 
        // alert(JSON.stringify(this.payUrl));
        // alert(JSON.stringify(this.orderId));
        // alert(channel);
        // alert(JSON.stringify(this.couponId));
        // alert(JSON.stringify(spbillCreateIp));
        let res = await pay(this.payUrl,this.orderId,channel ,this.couponId,spbillCreateIp);
        if(res.error_code === 2000){
          // alert(JSON.stringify(res));
          if(res.data.isZero === 1){
            // 
            Toast({
              message:'',
              duration:1500
            })
            setTimeout(() => {
              //   
              common.removeStorage('H5_payMent_time');
            common.removeStorage('H5_timeflag');
            // ID +  + 
            localStorage.removeItem('H5_fees');   // 
            localStorage.removeItem('H5_order_state'); // 
            clearInterval(this.timeInter);
            // alert(this.orderState);
            if(this.orderState){
              //
              this.$router.push({name:'payToComplete',params:{flag:1}});
            }else{
              this.$router.push('payToComplete');
            }
          }, 1500);
          }else{
            let params = {};
            // id	appId		String(16)	wx8888888888888888	
            // 	timeStamp		String(32)	1414561699	
            // 	nonceStr		String(32)	5K8264ILTKCH16CQ2502SI8ZNMTM67VS	32
            // 	package		String(128)	prepay_id=123456789	prepay_idprepay_id=***
            // 	signType		String(32)	MD5	MD5HMAC-SHA256MD5
            // 	paySign		String(64)	C380BEC2BFD727A4B6845133519F3AD6	
            params.appId = res.data.appId;
            params.timeStamp = res.data.timeStamp;
            params.nonceStr = res.data.nonceStr;
            params.package ="prepay_id="+res.data.prepayId;
            params.signType ="MD5";
            params.paySign =res.data.sign;
            // 
            if (typeof WeixinJSBridge == "undefined"){
              if( document.addEventListener ){
                document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
              }else if (document.attachEvent){
                document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
              }
            }else{
              // alert('');
              this.onBridgeReady(params);
            }
          }
        }else{
          // alert(JSON.stringify(res));
          console.log(res,''+res.error_code);
        }
      },
      //
      onBridgeReady(params){
        let vm = this;
        // alert(JSON.stringify(params));
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest", {
            "appId":params.appId,     //
            "timeStamp":params.timeStamp,         //1970
            "nonceStr":params.nonceStr, //
            "package":params.package,
            "signType":params.signType,         //
            "paySign":params.paySign //
          },
          function(res){
            // alert(JSON.stringify(res));
            if(res.err_msg == "get_brand_wcpay_request:ok" ) {
              Toast({
                message:'',
                duration:1500
              })
              setTimeout(() => {
                common.removeStorage('H5_payMent_time');
              common.removeStorage('H5_timeflag');
              //  +  + 
              localStorage.removeItem('H5_fees');   // 
              localStorage.removeItem('H5_order_state'); // 
              clearInterval(this.timeInter);
              if(vm.orderState){
                //
                // alert('')
                vm.$router.push({name:'payToComplete',params:{flag:1}});
              }else{
                vm.$router.push('/payToComplete');
              }
            }, 1500);
            }else{
              console.log('');
            }
          }
        );
      },
      //
      goCounp(){
        localStorage.setItem('H5_isChoosed',true);
        localStorage.setItem('counpFlag',this.couponId);
        this.$router.push({
          name:'ticket'
        })
      },

      // 
      timeAdd(){
        if(this.timeInter){
          clearInterval(this.timeInter);
          this.timeInter = null;
        }
        this.timeInter = setInterval(()=>{
          this.time++;
          // console.log(this.time);
          if(this.time >= 60){
            this.timeflag = true;
            // 5
            common.setStorage('H5_timeflag',this.timeflag);
            clearInterval(this.timeInter);
          }
        },1000);
      },
      documentEvent(){
          let vm = this;
         if(document.visibilityState=='visible'){
           //      miunte 
          clearInterval(vm.timeInter);
          let timeStamp = Date.parse(new Date());
          let oldtimeStamp = common.getStorage('H5_payMent_time');
          let miunte = Number(timeStamp) - Number(oldtimeStamp);
          miunte += '';
          vm.time = miunte.substring(0,miunte.length-3);
          console.log(vm.time);
          if(vm.time >= 300){
            vm.timeflag = true;
            common.setStorage('H5_timeflag', vm.timeflag);
          }else{
            vm.timeAdd();
          }
        }else{
          if(vm.timeInter){
            clearInterval(vm.timeInter);
          }
        }
      }
    },
    computed: {
      // //
      'channel':function(){
        return parseInt(this.currentIndex + 1);
      },
      // 
      'payAmounts':function(){
        // alert(this.payFees)
        // alert(this.discounts)

        return ((this.payFees - this.discounts)>=0?(this.payFees - this.discounts):0).toFixed(2);
      },
      //
      'counpsTips':function(){
        return this.discounts >0 ? ('' + this.discounts + '') : (this.counpNum > 0 ?'':'');
      }
    },
    beforeCreate(){
      this.payFees = localStorage.getItem('H5_fees');   // 
    },
    created(){
      let vm = this;
      let timeStamp = Date.parse(new Date());
      common.setStorage('H5_payMent_time',timeStamp);
      common.setStorage('H5_timeflag', this.timeflag);
      //       > 5 
      // this.timeAdd();
      // document.addEventListener("visibilitychange", vm.documentEvent,false);
      this.payFees = localStorage.getItem('H5_fees');   // 
      // alert(localStorage.getItem('H5_fees'))
      // alert(this.payFees)

      if(this.discounts === undefined){
        this.discounts = 0;
      }
      // 
      this.getCounpNum();
      this._initScroll();
      // 

      this.$root.eventHub.$on('send-counp-info',e=>{
          this.discounts =  e.amount;
          this.couponId = e.couponId;
      });
        //
        this.$root.eventHub.$on('cancel-counp-info',()=>{
          this.discounts =  0;
          this.couponId = -1;
        })
    },
    mounted(){

    },
    activated () {
      // orderState
      var payOrderState = localStorage.getItem('H5_order_state'); // 
      alert(payOrderState);
      // 
      if(payOrderState == 1303){
        this.orderState = true;
      }else{
        this.orderState = false;
      }
      this.currentIndex = 0;
      this.timeAdd();
      // 
      this.orderId = localStorage.getItem('orderId');  // ID
      let timeStamp = Date.parse(new Date());
      common.setStorage('H5_payMent_time',timeStamp);
      document.addEventListener("visibilitychange", this.documentEvent,false);
      // 
      this.$root.eventHub.$on('send-counp-info',e=>{
          this.discounts =  e.amount;
          this.couponId = e.couponId;
      });
        //
      this.$root.eventHub.$on('cancel-counp-info',()=>{
          this.discounts =  0;
          this.couponId = -1;
      })
      //
      this.payFees = localStorage.getItem('H5_fees');   // 
//    alert("paymentorder"+this.orderId)
//    alert("paymentH5_fees"+this.payFees)
      let H5Repay = JSON.parse(localStorage.getItem('H5_repay'));
      this.getCounpNum();

      // 
      let payFees = Number(this.payFees); // 
      console.log(this.discounts);
//      let discounts = JSON.parse(localStorage.getItem('H5_discounts')); // 
//      console.log(payFees,discounts);
    },
    beforeRouteLeave(to,from,next){
      // console.log(to.path,from.path);
      // debugger
      // if(to.path === '/appointment'){
      // document.removeEventListener("visibilitychange", this.documentEvent);
      //   clearInterval(this.timeInter);
      // }
      // localStorage.removeItem('H5_discounts');
      //  localStorage.removeItem('H5_counpinfo');
      next();
    },
    beforeRouteEnter (to,from,next){
      if(from.path=='/appoint' || from.path=='/appointment' || from.path == '/orderInfo'){
        next(vm =>{
          // this.discounts = localStorage.getItem('H5_discounts')?0:localStorage.getItem('H5_discounts');
          // localStorage.getItem('H5_couponId');
          vm.payFees = Number(localStorage.getItem('H5_fees'));
          vm.orderId = localStorage.getItem('orderId');
        })
      }
      next();
    },
    // 
    deactivated(){
      this.discounts =  0;
      this.couponId = -1;
      this.time = '';
      clearInterval(this.timeInter);
      // common.setStorage('counpFlag',-1);
      document.removeEventListener("visibilitychange", this.documentEvent,false);
    },
  }
</script>

<style lang="stylus">
@import '../../common/css/base.stylus'
@import '../../common/css/mixin.stylus'
.paymessage-info
  line-height: 2.5rem;
#pay-ment
  position absolute
  width 100%
  top 0
  bottom 0
  background-color #F4F4F4
  color #1a1a1a
  .content
    // margin-top 4.5rem
    .coup
      .ct-item
        // border-bottom 1px solid #e5e5e5e5e5e5
      .coup-num
        margin-top .05rem
        color #FF5C5A
        font-size .875rem
      .coup-ic
        width .5625rem
        height .9375rem
        margin-top: .25rem
        margin-left: .5rem
        bg-img('../../assets/img/arrow_r')
  .ct-item
    padding .75rem 1.2rem
    background-color #ffffff
    font-size 1.1rem
    // border-top 1px solid #e5e5e5
  .ct-info
    padding .5rem 1.2rem
    font-size .9rem
  .payWays
    margin-top .5rem
    // border-bottom .0625rem solid #e5e5e5
    .pay
      .info
        font-size .875rem
        color #666
    .choose-ic
      width 1.625rem
      height 1.125rem
      top 50%
      right 1.2rem
      transform translateY(-50%)
      bg-img('../../assets/img/parting_mine_Selected')
    .pay-item
      padding-left 3.5rem
    .pay-ic
      width 1.875rem
      height 2.25rem
      top 50%
      left .75rem
      transform translateY(-50%)
    .yuer
        bg-img('../../assets/img/btn_zhanghao')
    .zhifubao
      bg-img('../../assets/img/btn_alipay')
      height 1.875rem
    .weixin
      bg-img('../../assets/img/btn_weixin')
      height 1.52rem
  .comfirm
    margin-top 3rem
    font-size 1rem
    height 2.5rem
    line-height 2.5rem
    span
      display inline-block
      width 45%
      // height 3rem
      // line-height 3rem
      color #FFF
      bg-img('../../assets/img/Background')
      border-radius 1.25rem
  .pay-ment-box
    position absolute
    width 100%
    // top 4.5rem
    top 0rem
    bottom 0
    overflow hidden
</style>





